@mixin desktop {
  @media (min-width: (1200px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (840px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (600px)) {
    @content;
  }
}
