  --color-gray: #e2eaed;
  --color-gray-nickel: #b9b9b8;
  --color-blue: #242f51;
  --color-sky: #2500f9;
  --color-orange: #feaa25;
  --color-mid-gray: #616368;
  --color-red: #f55767;
  --color-light-blue: #2563ff;
  --color-green: #40975f;
  --color-lavender-blush: #ffecef;
  --color-milk-punch: #fff6d3;
  --color-titan-white: #f4f4ff;
  --color-lily-white: #eaf9ff;
  --color-hint-green: #e4ffee;
  --color-manatee: #999ca5;
  --color-accordion: #f5f7fa;
