  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}

.burger__icon__decor::before, .burger__icon__decor::after {
  content: '';
  border-radius: 5px;
  position: absolute;
  width: 50px;
  height: 6px;
  background-color: var(--color-black);
  transition: all 0.5s ease-in-out;
}

.burger__icon__decor::before {
